'use client';

import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuthStore } from '@/lib/store';
import { moviesApi } from '@/lib/api/movies';
import { Movie } from '@/lib/types';
import { MovieCard } from '@/components/MovieCard';
import { Button } from '@/components/Button';
import { Wave } from '@/components/Wave';

export default function MoviesPage() {
  const router = useRouter();
  const isAuthenticated = useAuthStore((state) => state.isAuthenticated);
  const logout = useAuthStore((state) => state.logout);
  const [movies, setMovies] = useState<Movie[]>([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [error, setError] = useState<string>('');

  useEffect(() => {
    const { initialize } = useAuthStore.getState();
    initialize();
  }, []);

  useEffect(() => {
    const { isAuthenticated: auth } = useAuthStore.getState();
    if (!auth) {
      router.push('/login');
      return;
    }
    loadMovies();
  }, [page]);

  const loadMovies = async () => {
    try {
      setLoading(true);
      setError('');
      const response = await moviesApi.getAll(page, 8);
      setMovies(response.data);
      setTotalPages(response.totalPages);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Failed to load movies');
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  const handleCreate = () => {
    router.push('/movies/create');
  };

  const handleEdit = (movie: Movie) => {
    router.push(`/movies/${movie.id}/edit`);
  };

  if (loading) {
    return (
      <main className="container max-w-[1200px] mx-auto mt-9 px-7">
        <div className="flex items-center justify-center min-h-[400px]">
          <p className="text-white/70">Loading...</p>
        </div>
      </main>
    );
  }

  if (error && movies.length === 0) {
    return (
      <main className="container max-w-[1200px] mx-auto mt-9 px-7">
        <div className="flex items-center justify-center min-h-[400px]">
          <p className="text-red-400">{error}</p>
        </div>
      </main>
    );
  }

  if (movies.length === 0) {
    return (
      <section className="hero hero--empty flex flex-col items-center justify-center text-center px-4 py-[160px] pb-[160px] relative min-h-[420px]">
        <h2 className="hero__title text-[64px] font-bold mb-7 text-white leading-none tracking-[-0.02em] m-0">
          Your movie list is empty
        </h2>
        <Button onClick={handleCreate} size="md">
          Add a new movie
        </Button>
        <Wave />
      </section>
    );
  }

  return (
    <main className="container max-w-[1200px] mx-auto mt-9 px-7 relative pb-[160px]">
      <header className="topbar flex items-center justify-between my-9">
        <h2 className="page-title text-[40px] font-bold m-0 flex items-center">
          My movies
          <span
            className="plus ml-2 font-bold opacity-90 cursor-pointer text-[40px]"
            onClick={handleCreate}
          >
            +
          </span>
        </h2>
        <a
          className="logout text-white/90 no-underline font-semibold cursor-pointer hover:text-white transition-colors"
          onClick={handleLogout}
        >
          Logout â†—
        </a>
      </header>

      {error && (
        <div className="mb-4 p-4 bg-red-500/20 border border-red-500 rounded-lg">
          <p className="text-red-400">{error}</p>
        </div>
      )}

      <section className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-7 mb-12">
        {movies.map((movie) => (
          <MovieCard key={movie.id} movie={movie} onEdit={handleEdit} />
        ))}
      </section>

      {totalPages > 1 && (
        <nav className="pagination flex justify-center gap-2 mb-[120px]">
          <button
            className="pager bg-transparent border-0 text-white/90 px-3 py-2 rounded-md font-semibold cursor-pointer hover:text-white transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            onClick={() => setPage((p) => Math.max(1, p - 1))}
            disabled={page === 1}
          >
            Prev
          </button>
          {Array.from({ length: totalPages }, (_, i) => i + 1).map((p) => (
            <button
              key={p}
              className={`pager bg-transparent border-0 text-white/90 px-3 py-2 rounded-md font-semibold cursor-pointer hover:text-white transition-colors ${
                p === page
                  ? 'bg-[#33d583] font-bold text-[#06343a]'
                  : ''
              }`}
              onClick={() => setPage(p)}
            >
              {p}
            </button>
          ))}
          <button
            className="pager bg-transparent border-0 text-white/90 px-3 py-2 rounded-md font-semibold cursor-pointer hover:text-white transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
            disabled={page === totalPages}
          >
            Next
          </button>
        </nav>
      )}

      <Wave variant="large" />
    </main>
  );
}

